export default {
	data() {
		return {
			imgUrl: "",
			searchContent: "", //搜索内容
			long: "",
			lat: "",
			list: [], //搜索结果
		}
	},
	onLoad() {
		this.imgUrl = this.$store.state.imgUrl;
		uni.getStorage({
			key: "long",
			success: r=> {
				this.long = r.data;
				uni.getStorage({
					key: "lat",
					success: res=> {
						this.lat = res.data;
					}
				})
			}
		})
	},
	methods: {
		ss() {
			if(this.searchContent == "") {
				uni.showToast({
					title: '请输入搜索内容',
					duration: 1000,
					icon: 'none'
				});
			} else {
				this.api({
					method: "post",
					url: "Category/search_shop",
					data: {
						longitude: this.long,
						latitude: this.lat,
						page: 1,
						name: this.searchContent
					}
				}).then(res=> {
					console.log(res);
					if(res.data.date.length == 0) {
						this.list = [];
						// console.log('meishi');
					} else {
						this.list = res.data.date;
					}
				})
			}
		},
		toHomepage(item) {
			console.log('点击商家，信息为：');
			console.log(item);
			uni.navigateTo({
				url: '../../business/homepage?id=' + item.shop_id + "&distance=" + item.distance + "&shop_socre=" + item.shop_socre
			})
		}
	}
}
